* {
    margin: 0;
    padding: 0;
    border: 0;
}

body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;

    .box {
        position: relative;
        width: 50%;
        height: 100vh;
        overflow: hidden;

        img {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            object-fit: cover;
        }
    }

    .box:nth-child(2) {
        img {
            filter: grayscale(1) contrast(5)
        }

        img:nth-child(2){
            filter: grayscale(2) contrast(1) brightness(2);
            mix-blend-mode: hard-light;
        }
    }
}
